/*
    Components: Carousel
    ---
    Custom carousel
*/

.c-gallery {
    overflow: hidden;
    height: 0;

    .flickity-viewport {
        position: absolute;
        width: 100%;
    }

    @include mq(bp4) {
        padding-bottom: calc(56% - 12.8rem);
    }
}

.c-gallery__cell {
    height: 100%;
    transition: opacity $animation-duration-beta $animation-easing-alpha;
    @include margin-right($columns-gutter * 1);

    @include mq(bp3) {
        @include margin-right($columns-gutter * 2);
    }
}

.c-gallery__img {
    max-width: none;
    height: 100%;
}

/*! Flickity v1.1.1
http://flickity.metafizzy.co
---------------------------------------------- */

.flickity-enabled {
    position: relative;
}

.flickity-enabled:focus { outline: none; }

.flickity-viewport {
    overflow: hidden;
    position: relative;
    height: 100%;
}

.flickity-slider {
    position: absolute;
    width: 100%;
    height: 100%;
}

/* draggable */

.flickity-enabled.is-draggable {
    tap-highlight-color: transparent;
    user-select: none;
}

.flickity-enabled.is-draggable .flickity-viewport {
    cursor: grab;
}

.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
    cursor: -webkit-grabbing;
    cursor: grabbing;
}

/* ---- previous/next buttons ---- */

.flickity-prev-next-button {
    position: absolute;
    padding: 0;
    top: 50%;
    margin-top: -2rem;
    width: $columns-gutter * 2rem;
    height: $columns-gutter * 2rem;
    border: 0;
    background: white;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all $animation-duration-alpha $animation-easing-alpha;
    background-color: rgba(palette(color-neutral,xxxx-light),.975);
    box-shadow: 0 0 50px rgba(palette(color-neutral,xxxx-dark),.1);

    @include mq(bp2, max) {
        display: none;
    }
}

.flickity-prev-next-button:hover { background: white; }

.flickity-prev-next-button:focus {
    outline: none;
    box-shadow: 0 0 0 5px palette(color-alpha);
}

.flickity-prev-next-button:active {
    transform: translateY(.1rem);
}

.flickity-prev-next-button.previous {
    left: 0;

    @include mq(bp3) {
        left: $columns-gutter * 1rem;
    }
}

.flickity-prev-next-button.next {
    right: 0;

    @include mq(bp3) {
        right: $columns-gutter * 1rem;
    }
}

/* right to left */
.flickity-rtl .flickity-prev-next-button.previous {
    left: auto;
    right: 0;
}

.flickity-rtl .flickity-prev-next-button.next {
    right: auto;
    left: 0;
}

.flickity-prev-next-button:disabled {
    opacity: .3;
    cursor: auto;
}

.flickity-prev-next-button svg {
    width: 30%;
    height: 30%;
}

.flickity-prev-next-button .arrow {
    fill: palette(color-alpha);
}
